'use client'

import { useState } from 'react'
import { DayPicker } from 'react-day-picker'
import 'react-day-picker/dist/style.css'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

interface Event {
  id: string
  title: string
  start: Date
  end: Date
  address: string
  type: 'vedlikehold' | 'inspeksjon' | 'annet'
}

const mockEvents: Event[] = [
  {
    id: '1',
    title: 'Takvedlikehold',
    start: new Date(2024, 3, 15, 10, 0),
    end: new Date(2024, 3, 15, 12, 0),
    address: 'Bassengvegen 18',
    type: 'vedlikehold'
  },
  {
    id: '2',
    title: 'Inspeksjon',
    start: new Date(2024, 3, 16, 14, 0),
    end: new Date(2024, 3, 16, 15, 0),
    address: 'Bassengvegen 18',
    type: 'inspeksjon'
  }
]

export default function Planlegging() {
  const [selected, setSelected] = useState<Date>()
  const [events] = useState<Event[]>(mockEvents)
  const [selectedEvent, setSelectedEvent] = useState<Event | null>(null)

  const getDayEvents = (day: Date) => {
    return events.filter(
      event => event.start.toDateString() === day.toDateString()
    )
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Planlegging</h1>
        <Button className="bg-green-600 hover:bg-green-700">
          + Ny avtale
        </Button>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
        <div className="lg:col-span-3">
          <Card className="p-4">
            <DayPicker
              mode="single"
              selected={selected}
              onSelect={setSelected}
              modifiers={{
                hasEvents: (date) => getDayEvents(date).length > 0
              }}
              modifiersStyles={{
                hasEvents: {
                  fontWeight: 'bold',
                  backgroundColor: '#f3f4f6'
                }
              }}
              footer={
                selected && (
                  <div className="mt-4">
                    {getDayEvents(selected).map(event => (
                      <div
                        key={event.id}
                        className={`mt-2 p-2 rounded text-white cursor-pointer event-${event.type}`}
                        onClick={() => setSelectedEvent(event)}
                      >
                        <div className="font-medium">{event.title}</div>
                        <div className="text-sm">
                          {event.start.toLocaleTimeString('nb-NO', {
                            hour: '2-digit',
                            minute: '2-digit'
                          })}
                          {' - '}
                          {event.end.toLocaleTimeString('nb-NO', {
                            hour: '2-digit',
                            minute: '2-digit'
                          })}
                        </div>
                      </div>
                    ))}
                  </div>
                )
              }
            />
          </Card>
        </div>

        <div className="lg:col-span-1">
          {selectedEvent && (
            <Card className="p-4">
              <h2 className="font-semibold mb-3">Detaljer</h2>
              <div className="space-y-2">
                <p><span className="font-medium">Tittel:</span> {selectedEvent.title}</p>
                <p><span className="font-medium">Adresse:</span> {selectedEvent.address}</p>
                <p><span className="font-medium">Type:</span> {selectedEvent.type}</p>
                <p><span className="font-medium">Start:</span> {
                  selectedEvent.start.toLocaleString('nb-NO', {
                    day: '2-digit',
                    month: '2-digit',
                    year: 'numeric',
                    hour: '2-digit',
                    minute: '2-digit'
                  })
                }</p>
                <p><span className="font-medium">Slutt:</span> {
                  selectedEvent.end.toLocaleString('nb-NO', {
                    day: '2-digit',
                    month: '2-digit',
                    year: 'numeric',
                    hour: '2-digit',
                    minute: '2-digit'
                  })
                }</p>
              </div>
            </Card>
          )}
        </div>
      </div>
    </div>
  )
} 